/* ===== LAYOUT ===== */
.layout {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* ===== HEADER ===== */
.header {
  background: linear-gradient(135deg, #1a1040 0%, #0f0f1a 40%, #0d1b2a 100%);
  border-bottom: 1px solid var(--border);
  padding: 18px 0;
  position: relative;
  overflow: hidden;
}

.header::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -20%;
  width: 60%;
  height: 200%;
  background: radial-gradient(ellipse, rgba(108, 99, 255, 0.15) 0%, transparent 70%);
  pointer-events: none;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
  position: relative;
  z-index: 1;
}

.logo h1 {
  margin: 0;
  font-size: 1.6rem;
  font-weight: 800;
  background: linear-gradient(135deg, #a78bfa, #6C63FF, #60a5fa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.02em;
}

.subtitle {
  margin: 4px 0 0;
  font-size: 0.9rem;
  color: var(--text-muted);
  font-weight: 400;
}

.event {
  margin: 4px 0 0;
  font-size: 0.82rem;
  color: var(--primary-light);
  font-weight: 500;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}

.language-toggle {
  display: none;
}

.lang-btn {
  display: none;
}

/* ===== NAVIGATION ===== */
.navigation {
  background: rgba(15, 15, 26, 0.85);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border-bottom: 1px solid var(--border);
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  gap: 4px;
  overflow-x: auto;
  scrollbar-width: none;
}

.nav-content::-webkit-scrollbar {
  display: none;
}

.nav-link {
  color: var(--text-muted);
  text-decoration: none;
  padding: 14px 14px;
  font-size: 0.88rem;
  font-weight: 500;
  transition: color var(--transition);
  white-space: nowrap;
  border-bottom: 2px solid transparent;
  position: relative;
}

.nav-link:hover {
  color: var(--text);
}

.nav-link.active {
  color: var(--primary-light);
  border-bottom-color: var(--primary);
}

/* Classroom nav highlight */
.nav-link-classroom {
  color: var(--accent) !important;
}

.nav-link-classroom.active {
  color: var(--accent) !important;
  border-bottom-color: var(--accent) !important;
}

/* ===== MAIN CONTENT ===== */
.main-content {
  flex: 1;
  background: var(--bg);
  padding: 48px 0;
}

/* ===== FOOTER ===== */
.footer {
  background: var(--bg-card);
  border-top: 1px solid var(--border);
  padding: 24px 0;
  text-align: center;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.footer-content p {
  margin: 4px 0;
  color: var(--text-dim);
  font-size: 0.85rem;
}

.footer-content p:first-child {
  color: var(--text-muted);
  font-weight: 500;
}

/* ===== PRESENTATION MODE OVERRIDES ===== */
body.presentation-mode .navigation {
  display: none;
}

body.presentation-mode .footer {
  display: none;
}

body.presentation-mode .header {
  padding: 12px 0;
}

body.presentation-mode .logo h1 {
  font-size: 1.2rem;
}

body.presentation-mode .main-content {
  padding: 0;
  font-size: 1.35rem;
}

/* ===== RESPONSIVE ===== */
@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    text-align: center;
  }

  .logo h1 {
    font-size: 1.3rem;
  }

  .nav-content {
    padding: 0 12px;
    gap: 0;
  }

  .nav-link {
    padding: 12px 10px;
    font-size: 0.82rem;
  }

  .main-content {
    padding: 24px 0;
  }
}