:root {
  /* Dark palette */
  --primary: #6C63FF;
  --primary-dark: #4f46e5;
  --primary-light: #8b83ff;
  --accent: #10b981;
  --accent-dark: #059669;
  --warning: #f59e0b;
  --danger: #ef4444;

  /* Backgrounds */
  --bg: #0f0f1a;
  --bg-card: #1a1a2e;
  --bg-surface: #16213e;
  --bg-elevated: #1e2a45;

  /* Text */
  --text: #e2e8f0;
  --text-muted: #94a3b8;
  --text-dim: #64748b;

  /* Borders */
  --border: rgba(108, 99, 255, 0.2);
  --border-hover: rgba(108, 99, 255, 0.5);

  /* Code */
  --code-bg: #0d1117;
  --code-text: #79c0ff;
  --code-green: #56d364;
  --code-comment: #8b949e;

  /* Shadows */
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.4);
  --shadow-md: 0 4px 20px rgba(0, 0, 0, 0.5);
  --shadow-glow: 0 0 20px rgba(108, 99, 255, 0.3);
  --shadow-glow-accent: 0 0 20px rgba(16, 185, 129, 0.3);

  /* Font */
  --font-body: 'Inter', system-ui, -apple-system, sans-serif;
  --font-code: 'Fira Code', 'Consolas', monospace;
  --font-size-base: 16px;

  /* Spacing */
  --radius: 12px;
  --radius-sm: 8px;
  --radius-lg: 16px;
  --radius-pill: 999px;

  /* Transitions */
  --transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-body);
  font-size: var(--font-size-base);
  background-color: var(--bg);
  color: var(--text);
  line-height: 1.7;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-card);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-dark);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary);
}

/* Selection */
::selection {
  background: rgba(108, 99, 255, 0.35);
  color: var(--text);
}

/* Inline code */
code {
  font-family: var(--font-code);
  font-size: 0.9em;
}

/* Links */
a {
  color: var(--primary-light);
  text-decoration: none;
  transition: color var(--transition);
}

a:hover {
  color: var(--primary);
}

/* Focus outline for accessibility */
:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 3px;
  border-radius: var(--radius-sm);
}

/* Animation utilities */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shimmer {
  0% {
    background-position: -200% center;
  }

  100% {
    background-position: 200% center;
  }
}

@keyframes pulse-glow {

  0%,
  100% {
    box-shadow: 0 0 15px rgba(108, 99, 255, 0.3);
  }

  50% {
    box-shadow: 0 0 30px rgba(108, 99, 255, 0.6);
  }
}

.App {
  min-height: 100vh;
}

/* ===== PRESENTATION MODE CONTROLS ===== */
.presentation-btn {
  position: fixed;
  bottom: 24px;
  right: 24px;
  z-index: 9999;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  border: none;
  border-radius: var(--radius-pill);
  padding: 12px 22px;
  font-family: var(--font-body);
  font-size: 0.9rem;
  font-weight: 700;
  cursor: pointer;
  box-shadow: 0 4px 20px rgba(108, 99, 255, 0.4);
  transition: all var(--transition);
  letter-spacing: 0.02em;
}

.presentation-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(108, 99, 255, 0.6);
}

.presentation-btn.active {
  background: linear-gradient(135deg, #374151, #1f2937);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.5);
}

.presentation-nav {
  position: fixed;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 9998;
  display: flex;
  align-items: center;
  gap: 12px;
  background: rgba(26, 26, 46, 0.9);
  backdrop-filter: blur(16px);
  border: 1px solid var(--border);
  border-radius: var(--radius-pill);
  padding: 10px 20px;
  box-shadow: var(--shadow-md);
}

.slide-btn {
  background: transparent;
  border: 1px solid var(--border);
  color: var(--text);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1rem;
  transition: all var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide-btn:hover:not(:disabled) {
  background: rgba(108, 99, 255, 0.2);
  border-color: var(--primary);
  color: var(--primary-light);
}

.slide-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.slide-counter {
  font-family: var(--font-body);
  font-size: 0.95rem;
  color: var(--text-muted);
  min-width: 60px;
  text-align: center;
}

.slide-current {
  color: var(--primary-light);
  font-weight: 700;
  font-size: 1.1rem;
}

.slide-sep {
  color: var(--text-dim);
}

.slide-total {
  color: var(--text-muted);
}