/* ===========================
   PAGES.CSS — Dark UI Redesign
   =========================== */

/* Card container */
.content {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 24px;
  animation: fadeInUp 0.4s ease both;
}

.card {
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: 40px 48px;
  box-shadow: var(--shadow-md);
}

.card h1 {
  font-size: 2rem;
  font-weight: 800;
  background: linear-gradient(135deg, #a78bfa, #6C63FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 8px;
  letter-spacing: -0.02em;
}

.card .subtitle {
  color: var(--text-muted);
  font-size: 1rem;
  margin-bottom: 4px;
}

.card .event-info,
.card .organization {
  font-size: 0.85rem;
  color: var(--text-dim);
}

/* ===== LAB SECTIONS ===== */
.lab-section {
  margin: 28px 0;
  padding: 24px 28px;
  background: var(--bg-surface);
  border-radius: var(--radius);
  border-left: 3px solid var(--primary);
  transition: border-color var(--transition), box-shadow var(--transition);
}

.lab-section:hover {
  border-left-color: var(--primary-light);
  box-shadow: -3px 0 12px rgba(108, 99, 255, 0.15);
}

.lab-section h2 {
  color: var(--primary-light);
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 1.15rem;
  font-weight: 700;
}

.lab-section ol,
.lab-section ul {
  padding-left: 20px;
  color: var(--text);
}

.lab-section li {
  margin: 8px 0;
  line-height: 1.7;
}

/* ===== INLINE CODE ===== */
.inline-code {
  background: rgba(108, 99, 255, 0.15);
  color: var(--primary-light);
  font-family: var(--font-code);
  padding: 2px 8px;
  border-radius: 6px;
  font-size: 0.87em;
  border: 1px solid rgba(108, 99, 255, 0.25);
}

/* ===== CODE BLOCKS ===== */
.code-block {
  background: var(--code-bg);
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: var(--radius);
  padding: 20px 24px;
  margin: 16px 0;
  font-family: var(--font-code);
  font-size: 0.9rem;
  position: relative;
  overflow-x: auto;
}

.code-block code {
  color: var(--code-text);
  display: block;
  line-height: 1.8;
}

.code-block code::before {
  content: '$ ';
  color: var(--code-green);
}

.code-block pre {
  margin: 0;
  white-space: pre-wrap;
  word-break: break-word;
}

.code-block pre code::before {
  content: none;
}

.command-line {
  margin: 4px 0;
}

.command-line code {
  color: var(--text-muted);
}

/* ===== REFLECTION SECTION ===== */
.reflection-section {
  background: rgba(96, 165, 250, 0.07);
  border: 1px solid rgba(96, 165, 250, 0.2);
  border-radius: var(--radius);
  padding: 24px 28px;
  margin: 28px 0;
}

.reflection-section h2 {
  color: #60a5fa;
  margin-top: 0;
  font-size: 1.1rem;
}

.reflection-section li {
  color: var(--text-muted);
  margin: 8px 0;
}

/* ===== HIGHLIGHT BOXES ===== */
.highlight {
  background: rgba(108, 99, 255, 0.1);
  border: 1px solid rgba(108, 99, 255, 0.25);
  border-radius: var(--radius);
  padding: 20px 24px;
  margin: 24px 0;
}

.highlight h4 {
  color: var(--primary-light);
  margin: 0 0 10px 0;
  font-size: 1rem;
  font-weight: 700;
}

.highlight p {
  color: var(--text-muted);
  margin: 6px 0;
  font-size: 0.95rem;
}

.warning-highlight {
  background: rgba(245, 158, 11, 0.1);
  border: 1px solid rgba(245, 158, 11, 0.3);
  border-radius: var(--radius);
  padding: 16px 20px;
  margin: 20px 0;
  color: #fcd34d;
}

/* ===== PREREQUISITES / SETUP / NEXT STEPS ===== */
.prerequisites-section,
.setup-section {
  margin: 28px 0;
}

.prerequisites-section h2,
.setup-section h2 {
  color: var(--primary-light);
  font-size: 1.2rem;
  margin-bottom: 12px;
}

.prerequisites-section ul {
  list-style: none;
  padding: 0;
}

.prerequisites-section ul li::before {
  content: '✓ ';
  color: var(--accent);
  font-weight: 700;
}

.next-steps {
  background: rgba(16, 185, 129, 0.08);
  border: 1px solid rgba(16, 185, 129, 0.25);
  border-radius: var(--radius);
  padding: 24px 28px;
  margin: 28px 0;
}

.next-steps h2 {
  color: var(--accent);
  margin-top: 0;
}

.action-buttons {
  margin-top: 20px;
}

/* ===== SSH SECTION ===== */
.ssh-section {
  background: rgba(245, 158, 11, 0.07);
  border: 1px solid rgba(245, 158, 11, 0.2);
  border-radius: var(--radius);
  padding: 24px 28px;
  margin: 28px 0;
}

.ssh-section h2 {
  color: #fbbf24;
  margin-top: 0;
}

/* ===== TEMPLATE SECTION ===== */
.template-section {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 24px 28px;
  margin: 28px 0;
}

/* ===== BUTTONS ===== */
.btn {
  display: inline-block;
  padding: 10px 22px;
  border-radius: var(--radius-pill);
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  font-family: var(--font-body);
  transition: all var(--transition);
  border: 2px solid transparent;
  cursor: pointer;
  letter-spacing: 0.01em;
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  box-shadow: 0 4px 12px rgba(108, 99, 255, 0.3);
}

.btn-primary:hover {
  box-shadow: 0 6px 20px rgba(108, 99, 255, 0.5);
  transform: translateY(-2px);
  color: white;
}

.btn-secondary {
  background: var(--bg-elevated);
  color: var(--text-muted);
  border-color: var(--border);
}

.btn-secondary:hover {
  color: var(--text);
  border-color: var(--primary);
  transform: translateY(-2px);
}

.btn-outline {
  background: transparent;
  color: var(--primary-light);
  border-color: rgba(108, 99, 255, 0.4);
}

.btn-outline:hover {
  background: rgba(108, 99, 255, 0.1);
  border-color: var(--primary);
  transform: translateY(-2px);
}

.btn-classroom {
  background: linear-gradient(135deg, var(--accent), var(--accent-dark));
  color: white;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.btn-classroom:hover {
  box-shadow: 0 6px 20px rgba(16, 185, 129, 0.5);
  transform: translateY(-2px);
  color: white;
}

.btn-large {
  padding: 14px 36px;
  font-size: 1.05rem;
}

/* ===== NAVIGATION LINKS ===== */
.navigation-links {
  display: flex;
  justify-content: space-between;
  margin: 40px 0 8px;
  flex-wrap: wrap;
  gap: 16px;
}

.navigation-links .btn {
  flex: 1;
  min-width: 140px;
  text-align: center;
}

/* ===== HOME — WELCOME ===== */
.welcome-section {
  text-align: center;
  margin: 40px 0;
  padding: 40px 32px;
  background: linear-gradient(135deg, rgba(108, 99, 255, 0.15) 0%, rgba(79, 70, 229, 0.08) 100%);
  border: 1px solid rgba(108, 99, 255, 0.2);
  border-radius: var(--radius-lg);
  position: relative;
  overflow: hidden;
}

.welcome-section::before {
  content: '';
  position: absolute;
  top: -30px;
  right: -30px;
  width: 120px;
  height: 120px;
  background: radial-gradient(circle, rgba(108, 99, 255, 0.3) 0%, transparent 70%);
  border-radius: 50%;
}

.welcome-section h2 {
  font-size: 1.7rem;
  font-weight: 800;
  color: var(--text);
  margin-bottom: 12px;
}

.welcome-section p {
  color: var(--text-muted);
  max-width: 600px;
  margin: 0 auto;
}

/* ===== HOME — LAB CARDS ===== */
.workshop-overview {
  margin: 40px 0;
}

.workshop-overview>h2 {
  color: var(--text);
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 24px;
}

.lab-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 16px;
  margin: 0;
}

.lab-card {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 24px 20px;
  text-align: center;
  transition: transform var(--transition), box-shadow var(--transition), border-color var(--transition);
  cursor: default;
}

.lab-card:hover {
  transform: translateY(-6px);
  box-shadow: var(--shadow-glow);
  border-color: rgba(108, 99, 255, 0.4);
}

.lab-card h3 {
  color: var(--primary-light);
  margin: 0 0 10px;
  font-size: 1rem;
  font-weight: 700;
}

.lab-card p {
  color: var(--text-muted);
  font-size: 0.88rem;
  margin-bottom: 18px;
  line-height: 1.5;
}

/* Classroom card — verde */
.lab-card-classroom {
  border-color: rgba(16, 185, 129, 0.3);
}

.lab-card-classroom:hover {
  box-shadow: var(--shadow-glow-accent);
  border-color: rgba(16, 185, 129, 0.55);
}

.lab-card-classroom h3 {
  color: var(--accent) !important;
}

/* ===== HOME — QUICK START ===== */
.quick-start {
  margin: 40px 0;
}

.quick-start>h2 {
  color: var(--text);
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 24px;
}

.steps {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px;
}

.step {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  max-width: 280px;
  flex: 1;
  min-width: 200px;
}

.step-number {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 1rem;
  flex-shrink: 0;
  box-shadow: 0 4px 12px rgba(108, 99, 255, 0.35);
}

.step-content h4 {
  color: var(--text);
  font-weight: 700;
  margin-bottom: 4px;
}

.step-content p {
  color: var(--text-muted);
  font-size: 0.88rem;
}

/* ===== HOME — RESOURCES PREVIEW ===== */
.resources-preview {
  text-align: center;
  margin: 40px 0 0;
  padding: 32px;
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
}

.resources-preview h2 {
  color: var(--text);
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.resources-preview p {
  color: var(--text-muted);
  margin-bottom: 20px;
  font-size: 0.95rem;
}

/* ===== RESOURCES PAGE ===== */
.links-section h2,
.commands-section h2,
.troubleshooting-section h2,
.final-notes-section h2 {
  color: var(--text);
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 20px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--border);
}

.links-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 16px;
  margin: 20px 0 32px;
}

.link-card {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 20px;
  text-align: center;
  transition: transform var(--transition), border-color var(--transition), box-shadow var(--transition);
}

.link-card:hover {
  transform: translateY(-4px);
  border-color: rgba(108, 99, 255, 0.4);
  box-shadow: var(--shadow-glow);
}

.link-card h3 {
  color: var(--primary-light);
  margin: 0 0 14px;
  font-size: 0.95rem;
}

.command-category {
  margin: 24px 0;
}

.command-category h3 {
  color: var(--text-muted);
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 10px;
}

.issues-list {
  margin: 20px 0;
}

.issue-item {
  background: var(--bg-surface);
  border-left: 3px solid var(--danger);
  border-radius: 0 var(--radius-sm) var(--radius-sm) 0;
  padding: 16px 20px;
  margin: 12px 0;
}

.issue-item h4 {
  color: #fca5a5;
  margin: 0 0 8px;
  font-size: 0.95rem;
}

.issue-item p {
  color: #6ee7b7;
  margin: 0;
  font-size: 0.9rem;
}

.tips-list {
  margin: 20px 0;
}

.tip-item {
  display: flex;
  align-items: center;
  gap: 14px;
  margin: 12px 0;
  padding: 14px 18px;
  background: var(--bg-surface);
  border-radius: var(--radius-sm);
  border: 1px solid var(--border);
  transition: border-color var(--transition);
}

.tip-item:hover {
  border-color: rgba(108, 99, 255, 0.3);
}

.tip-icon {
  font-size: 1.2rem;
  flex-shrink: 0;
}

/* ===== GITHUB CLASSROOM PAGE ===== */
.classroom-intro {
  background: rgba(16, 185, 129, 0.07);
  border-left: 3px solid var(--accent);
}

.classroom-intro h2 {
  color: var(--accent) !important;
}

.classroom-cta {
  text-align: center;
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.2), rgba(5, 150, 105, 0.1));
  border: 1px solid rgba(16, 185, 129, 0.3);
  padding: 48px 32px;
  border-radius: var(--radius-lg);
  margin: 40px 0;
}

.classroom-cta h3 {
  color: var(--text);
  font-size: 1.6rem;
  font-weight: 800;
  margin-bottom: 10px;
}

.classroom-cta p {
  color: var(--text-muted);
  margin-bottom: 28px;
}

/* ===== RESPONSIVE ===== */
@media (max-width: 768px) {
  .card {
    padding: 24px 20px;
    border-radius: var(--radius);
  }

  .card h1 {
    font-size: 1.5rem;
  }

  .lab-cards {
    grid-template-columns: 1fr;
  }

  .steps {
    flex-direction: column;
    align-items: flex-start;
  }

  .links-grid {
    grid-template-columns: 1fr;
  }

  .navigation-links {
    flex-direction: column;
  }

  .welcome-section h2 {
    font-size: 1.3rem;
  }
}

/* ===== PRESENTATION MODE ===== */
body.presentation-mode .content {
  max-width: 100%;
  padding: 0;
}

body.presentation-mode .card {
  border-radius: 0;
  border: none;
  min-height: calc(100vh - 70px);
  padding: 40px 80px;
}

body.presentation-mode .card h1 {
  font-size: 2.8rem;
}

body.presentation-mode .lab-section h2 {
  font-size: 1.5rem;
}

body.presentation-mode li,
body.presentation-mode p {
  font-size: 1.2rem;
}

body.presentation-mode .code-block code {
  font-size: 1.1rem;
}

/* Slide transition */
.slide-enter {
  opacity: 0;
  transform: translateX(40px);
}

.slide-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: all 0.3s ease;
}

.slide-exit {
  opacity: 1;
}

.slide-exit-active {
  opacity: 0;
  transform: translateX(-40px);
  transition: all 0.3s ease;
}